/* 페이지 전체 *********************************************/
.content_js {
  /* margin-top: 30px;
  margin-left: 20px; */
  height: 100vh;
  background: linear-gradient(to bottom, #3459a3, #3380a3);
  border: 1px solid gray;
}

/* 배경 */
html,
body {
  margin: 0;
  height: 100%;
}

/* 타이틀 */
.content_title {
  font-size: 33px;
  font-weight: bold;
  color: white;
  margin-top: 30px;
  margin-left: 50px;
}

/* 제목 밑에 날씨 데이터들 */
.weatherData {
  margin-top: 40px;
  margin-left: 50px;
}

/* 현재 날씨 **********************************************/

/* border 하얀 밑줄 */
.currentLocation {
  border-bottom: 2px solid #ffffff;
  width: 20%;
  /* padding-bottom: 3px; */
}

/* 위치 아이콘 */
.locationIcon {
  /* background-image: url(../image//btn_map.png); */
  width: 35px;
  height: 35px;
}

/* 지역 이름 */
.currentLocationName {
  font-size: 36px;
  color: #ffffff;
  /* vertical-align: top; */
  margin-left: 10px;
}

/* v모양 선택 아이콘 */
.choiceIcon {
  float: right;
  width: 13px;
  height: 10px;
  margin-top: 20px;
  margin-right: 2%;
}

/* 현재, 인터벌 날씨 박스의 부모 태그 **********************************************/
.weatherBox {
  display: flex;
}

/* 현재 날씨 박스 **********************************************/
.currentWeatherUl {
  border: solid 3px #ffffff;
  color: #ffffff;
  /* width: 20%; */
  width: 240px;
  height: 170px;
  display: flex;
  flex-direction: column;
  background-color: #2c528b;
  text-align: center;
  padding: 0px;
  padding-right: 50px;
  /* border-right-width: 40px; */
}

/* 현재 */
.currentLi {
  /* border: 1px solid gray; */
  /* margin-top: 40px; */
  margin-bottom: 0px;
  /* margin-right: 50px; */
  /* margin-left: 25%; */
  font-size: 20px;
  /* width: 100%; */
}

/* 아이콘 */
.currentIconLi {
  /* display: inline-block; */
  /* border: 1px solid gray; */
  margin: 0px;
  margin-bottom: 20px;
  padding: 0px;
  /* margin-top: 10px; */
  /* margin-right: 5px; */
  /* width: 90px; */
  /* vertical-align: middle; */
  float: left;
  margin-left: 15px;
}

/* 온도 */
.currentTempLi {
  /* border: 1px solid gray; */
  font-size: 30px;
  margin-top: 30px;
  text-align: right;
  margin-left: 30px;
}

/* 인터벌 날씨 **********************************************/

.intervalWeatherUl {
  /* border-bottom: solid 3px #ffffff; */
  color: #ffffff;
  width: 40%;
  background-color: #2c528b;
  text-align: center;
  padding-right: 10px;
}

.date {
  color: black;
  background-color: #ffc500;
  /* text-align: center; */
  font-size: 18px;
  width: 100%;
  height: 15%;
  margin-top: 10px;
  margin-right: 40px;
  padding-right: 10px;
}

.time {
  font-size: 15px;
  margin-top: 10px;
}

.temp {
  margin-top: 10px;
  font-size: 23px;
}

/* 친구 찾기  **********************************************/
.findFriendsUl {
  background-color: #2c528b;
  border-left: 2px solid gray;
  width: 150px;
}

/* 친구 찾기 버튼 */
.findFriends {
  color: black;
  background-color: #ffc500;
  font-size: 18px;
  margin-top: 10px;
  width: 80%;
  height: 15%;
  margin-right: 30px;
}

.friends {
  margin-top: 10px;
  /* margin-bottom: 10px; */
  font-size: 18px;
  color: #ffffff;
  /* border: 2px solid gray; */
}

/* 햄버거 **********************************************/

.hamburger {
  /* display: none; */
  position: absolute;
  top: 30px;
  right: 42px;
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  margin: 3px;
  transition: all 0.4s ease;
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-3px, 6px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  transform: rotate(45deg) translate(-3px, -6px);
}

.hamburger {
  /* display: block; */
}

/* 로그아웃 버튼 **********************************************/
.logoutBtn {
  font-size: 20px;
}
